import React from "react";
import { Container } from "react-bootstrap";
import HTMLFlipBook from "react-pageflip";
import './book.scss';
import data from "./data.json";

const PageCover = React.forwardRef((props, ref) => {
  return (
    <div className="page page-cover" ref={ref} data-density="hard">
      <div className="page-content">
        <h2>{props.children}</h2>
      </div>
    </div>
  )
});

const Page = React.forwardRef((props, ref) => {
  return (
    <div className="page" ref={ref}>
      <div className="page-content">
        <h2 className="page-header">{props.data.header}</h2>
        <img className="page-image" src={require(`./images/${props?.data?.image}`).default} alt="" />
        <div className="page-text">{props.data.body}</div>
      </div>
    </div>
  )
});

const Book = () => {
  return (
    <Container fluid="md">
      <HTMLFlipBook 
        width={550} 
        height={733} 
        size="stretch" 
        maxWidth={1000}
        maxHeight={1500}
        showCover={true} 
        maxShadowOpacity={0.5}
        mobileScrollSupport={true} 
        className="demo-book html-book flip-book"
      >
        <PageCover className="page page-cover">THANK YOU MOBEEN</PageCover>
        {
          data.map((e) => {
            return (
              <Page data={e} key={e.header} />
            )
          })
        }
      </HTMLFlipBook>
    </Container>
    
  )
};

export default Book;
